import React, { useState } from "react";
import authSvg from "../assests/auth.svg";
import { ToastContainer, toast } from "react-toastify";
import axios from "axios";
import { Link } from "react-router-dom";
// import { authenticate, isAuth } from "../helpers/auth";
// import { Redirect } from "react-router-dom";

const Register = ({ history }) => {
  const [formData, setFormData] = useState({
    firstname: "",
    lastname: "",
    username: "",
    email: "",
    phone: "",
    password: "",
    textChange: "Sign Up",
  });

  const { email, password, firstname, lastname, username, phone, textChange } =
    formData;
  const handleChange = (text) => (e) => {
    setFormData({ ...formData, [text]: e.target.value });
  };

  const userRedirect = () => {
    if (typeof window !== "undefined") {
      window.location.href =
        "https://app-inext-auth.herokuapp.com/activate/token";
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (firstname && lastname && username && email && password) {
      if (password.length < 6) {
        return toast.error("Password must be six character");
      } else {
        setFormData({ ...formData, textChange: "Submitting" });
        axios
          .post("https://i-next-backend.herokuapp.com/api/users/register", {
            firstname,
            lastname,
            username,
            phone,
            email,
            password,
          })
          .then((res) => {
            setFormData({
              ...formData,
              firstname: "",
              lastname: "",
              username: "",
              email: "",
              phone: "",
              password: "",
              textChange: "Submit",
            });

            // console.log((res.data.message)
            toast.success(res.data.message) && userRedirect();
          })
          .catch((err) => {
            setFormData({
              ...formData,
              firstname: "",
              lastname: "",
              username: "",
              email: "",
              phone: "",
              password: "",
              textChange: "Sign up",
            });
            //toast.error(err.response.data.message);
            console.log(err.response.data.message);
            toast.error("Sorry, something went wrong. Please try again later.");
          });
      }
    } else {
      toast.error("Please fill all fields");
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 text-gray-900 flex justify-center">
      {/* {isAuth() ? <Redirect to="/" /> : <Redirect to="/" />} */}
      <ToastContainer />
      <div className="max-w-screen-xl m-0 sm:m-20 bg-white shadow sm:rounded-lg flex justify-center flex-1">
        <div className="flex-1 bg-indigo-100 text-center hidden lg:flex">
          <div
            className="m-6 xl:m-16 w-full bg-contain bg-center bg-no-repeat"
            style={{ backgroundImage: `url(${authSvg})` }}
          ></div>
        </div>
        <div className="lg:w-1/2 xl:w-6/12 p-6 sm:p-12">
          <div className="mt-0 flex flex-col items-center">
            <h4 className="text-2xl">Create your account</h4>
            <div className="my-2"></div>
            <form
              className="w-full flex-2 mt-2 text-indigo-500"
              onSubmit={handleSubmit}
            >
              <div className="mx-auto flex flex-row">
                <input
                  className="w-full max-w-xs px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mr-8 mt-5"
                  type="text"
                  placeholder="Enter your firstname"
                  onChange={handleChange("firstname")}
                  value={firstname}
                />
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white  mt-5"
                  type="text"
                  placeholder="Enter your lastname(s)"
                  onChange={handleChange("lastname")}
                  value={lastname}
                />
              </div>
              <div className="mx-auto flex flex-row">
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mr-8 mt-5"
                  type="phone"
                  placeholder="Enter your phone number"
                  onChange={handleChange("phone")}
                  value={phone}
                />
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5"
                  type="email"
                  placeholder="Enter your email address"
                  onChange={handleChange("email")}
                  value={email}
                />
              </div>
              <div className="mx-auto flex flex-row">
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white  mr-8 mt-5"
                  type="text"
                  placeholder="Choose a username"
                  onChange={handleChange("username")}
                  value={username}
                />
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5"
                  type="password"
                  placeholder="Choose password"
                  onChange={handleChange("password")}
                  value={password}
                />
              </div>
              <button
                type="submit"
                className="mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none"
              >
                <i className="fas fa-user-plus fa 1x w-6  -ml-2" />
                <span className="ml-3">{textChange}</span>
              </button>
              <div className="my-12 border-b text-center">
                <div className="leading-none px-2 inline-block text-sm text-gray-600 tracking-wide font-medium bg-white transform translate-y-1/2">
                  Already have an account?{" "}
                  <Link to="/login">
                    <span className="text-indigo-500 font-bold">Sign in</span>
                  </Link>
                </div>
              </div>
              {/* <div className="flex flex-col items-center">
                <a
                  className="w-full max-w-xs font-bold shadow-sm rounded-lg py-3
           bg-indigo-100 text-gray-800 flex items-center justify-center transition-all duration-300 ease-in-out focus:outline-none hover:shadow focus:shadow-sm focus:shadow-outline mt-5"
                  href="/login"
                  target="_self"
                >
                  <i className="fas fa-sign-in-alt fa 1x w-6  -ml-2 text-indigo-500" />
                  <span className="ml-4">Sign In</span>
                </a>
              </div> */}
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Register;
