import React, { useState } from "react";
import authSvg from "../assests/welcome.svg";
import { ToastContainer, toast } from "react-toastify";
import axios from "axios";
import jwt from "jsonwebtoken";
import { Link } from "react-router-dom";
// import { isAuth } from "../helpers/auth";
// import { Redirect } from "react-router-dom";

const Activate = () => {
  const [tokenId, setTokenId] = useState({
    token: "",
    show: true,
  });
  // const [tokenId, setTokenId] = useState(null);

  // const { username, password } = formData;
  const { username, token } = tokenId;

  // useEffect(() => {
  // let { username } = jwt.decode(token);
  //   if (token) {
  //     setTokenId({ ...formData, username, token });
  //   }
  // }, []);
  // const { username } = jwt.decode(token);
  const handleChange = (text) => (e) => {
    setTokenId({ ...tokenId, [text]: e.target.value });
  };

  const userRedirect = () => {
    if (typeof window !== "undefined") {
      window.location.href = "https://ideanext.io";
    }
  };
  const handleSubmit = (e) => {
    console.log(tokenId);
    e.preventDefault();
    axios
      .post("https://i-next-backend.herokuapp.com/api/users/activation", {
        token,
      })
      .then((res) => {
        setTokenId({
          ...tokenId,
          show: false,
        });

        toast.success(res.data.message) && userRedirect();
      })
      .catch((err) => {
        //toast.error(err.response.data.errors);
        console.log(err.response.data);
        toast.info(
          "Sorry, token expire. Please click the Resent botton and try again later."
        );
      });
  };

  return (
    <div className="min-h-screen bg-gray-100 text-gray-900 flex justify-center">
      <ToastContainer />
      <div className="max-w-screen-xl m-0 sm:m-20 bg-white shadow sm:rounded-lg flex justify-center flex-1">
        <div className="lg:w-1/2 xl:w-6/12 p-6 sm:p-12">
          <div className="mt-4 flex flex-col">
            <h4 className="text-2xl">
              Welcome, <span className="text-green-700">firstname_here</span>.
            </h4>

            <form
              className="w-full flex-1 mt-10 text-indigo-500"
              onSubmit={handleSubmit}
            >
              <div className="flex flex-row">
                <input
                  className="w-full max-w-xs px-4 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mr-8 mt-5"
                  type="text"
                  placeholder="Paste token received via your email"
                  onChange={handleChange("token")}
                  // value={token}
                />
              </div>

              <div className="leading-none px-2 inline-block text-sm text-gray-600 tracking-wide font-medium bg-white transform translate-y-1/2">
                <Link to="#">
                  <span className="text-green-500 font-bold">
                    {" "}
                    Resent token{" "}
                  </span>
                </Link>
              </div>

              <div className="max-w-xs relative ">
                <button
                  type="submit"
                  className="mt-8 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none"
                >
                  <i className="fas fa-user-plus fa 1x w-6" />
                  <span className="ml-3">Confirm your identity</span>
                </button>
              </div>
            </form>

            <div className="leading-none mt-12 px-2 inline-block text-sm text-gray-600 tracking-wide font-medium bg-white transform translate-y-1/2">
              <Link to="/login">
                <span className="bg-red-700 text-white px-4 py-2 border font-bold">
                  {" "}
                  Logout{" "}
                </span>
              </Link>
            </div>
          </div>
        </div>
        <div className="flex-1 bg-indigo-100 text-center hidden lg:flex">
          <div
            className="m-12 xl:m-16 w-full bg-contain bg-center bg-no-repeat"
            style={{ backgroundImage: `url(${authSvg})` }}
          ></div>
        </div>
      </div>
    </div>
  );
};

export default Activate;
