import React, { useState } from "react";
import authSvg from "../assests/auth.svg";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";

const IChallenge = () => {
  const [iChallengeData, setIChallengeData] = useState({
    username: "",
    password: "",
    member_Ideanext: "",
    website: "",
    country: "",
    institutional_email: "",
    inst_org: "",
    dep_unit_org: "",
    title: "",
    textChange: "",
  });

  // const clear = () => {
  //   setIChallengeData({
  //     username: "",
  //     password: "",
  //     member_Ideanext: "",
  //     website: "",
  //     country: "",
  //     institutional_email: "",
  //     inst_org: "",
  //     dep_unit_org: "",
  //     title: "",
  //     textChange: "Sign Up for IChallenge",
  //   });
  // };

  const {
    username,
    password,
    member_Ideanext,
    website,
    country,
    inst_org,
    dep_unit_org,
    title,
    institutional_email,
    textChange,
  } = iChallengeData;

  const handleChange = (text) => (e) => {
    setIChallengeData({ ...iChallengeData, [text]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(iChallengeData);
    if (
      username &&
      password &&
      member_Ideanext &&
      website &&
      country &&
      institutional_email &&
      inst_org &&
      dep_unit_org &&
      title
    ) {
      if (password.length < 6) {
        return toast.error("Your Password must be six character");
      } else {
        setIChallengeData({ ...iChallengeData, textChange: "Submitting" });
        axios
          .post("https://i-next-backend.herokuapp.com/ichallenge/user/signup", {
            username,
            password,
            member_Ideanext,
            website,
            country,
            institutional_email,
            inst_org,
            dep_unit_org,
            title,
            textChange: "Submitted",
          })
          .then((res) => {
            setIChallengeData({
              username: "",
              password: "",
              member_Ideanext: "",
              website: "",
              country: "",
              institutional_email: "",
              inst_org: "",
              dep_unit_org: "",
              title: "",
              textChange: "Sign Up IChallenege",
            });
            toast.success(res.data.message) && userRedirect();
            console.log(res.data.message);
          })
          .catch((err) => {
            setIChallengeData({
              username: "",
              password: "",
              member_Ideanext: "",
              website: "",
              country: "",
              institutional_email: "",
              inst_org: "",
              dep_unit_org: "",
              title: "",
            });
            toast.error(err.response.data.message);
          });
      }
    } else {
      toast.error("Please fill all fields");
    }
  };
  const userRedirect = () => {
    if (typeof window !== "undefined") {
      window.location.href = "https://ideanext.io";
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 text-gray-900 flex justify-center">
      <ToastContainer />
      <div className="max-w-screen-xl m-0 sm:m-20 bg-white shadow sm:rounded-lg flex justify-center flex-1">
        <div className="flex-1 bg-indigo-100 text-center hidden lg:flex">
          <div
            className="m-12 xl:m-16 w-full bg-contain bg-center bg-no-repeat"
            style={{ backgroundImage: `url(${authSvg})` }}
          ></div>
        </div>
        <div className="lg:w-1/2 xl:w-6/12 p-6 sm:p-12">
          <div className="mt-5 flex flex-col items-center">
            <h1 className="text-2xl xl:text-3xl font-extrabold">
              Sign Up for Innovation Challenge
            </h1>

            <form
              className="w-full flex-2 mt-2 text-indigo-500"
              onSubmit={handleSubmit}
            >
              <div className="mx-auto flex flex-row">
                <input
                  className="w-full max-w-xs px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mr-8 mt-5"
                  type="text"
                  placeholder="Username"
                  onChange={handleChange("username")}
                  value={username}
                />
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white  mt-5"
                  type="password"
                  placeholder="Password"
                  onChange={handleChange("password")}
                  value={password}
                />
              </div>
              <div className="mx-auto flex flex-row">
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white  mr-8 mt-5"
                  type="text"
                  placeholder="Title"
                  onChange={handleChange("title")}
                  value={title}
                />
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5"
                  type="text"
                  placeholder="Institution/Org"
                  onChange={handleChange("inst_org")}
                  value={inst_org}
                />
              </div>
              <div className="mx-auto flex flex-row">
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white  mr-8 mt-5"
                  type="text"
                  placeholder="Dept/Unit/Org"
                  onChange={handleChange("dep_unit_org")}
                  value={dep_unit_org}
                />
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5"
                  type="text"
                  placeholder="Institutional Email"
                  onChange={handleChange("institutional_email")}
                  value={institutional_email}
                />
              </div>
              <div className="mx-auto flex flex-row">
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white  mr-8 mt-5"
                  type="text"
                  placeholder="Website"
                  onChange={handleChange("website")}
                  value={website}
                />
                <input
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5"
                  type="text"
                  placeholder="Country"
                  onChange={handleChange("country")}
                  value={country}
                />
              </div>
              <div className="mx-auto flex flex-row">
                <select
                  placeholder="Are you Member"
                  id="cars"
                  className="w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5"
                  onChange={handleChange("member_Ideanext")}
                >
                  <option value={member_Ideanext.value}>
                    Are you part of Idea Next Fora?
                  </option>
                  <option
                    value={member_Ideanext.value}
                    onChange={handleChange("member_Ideanext")}
                  >
                    Yes
                  </option>
                  <option
                    value={member_Ideanext.value}
                    onChange={handleChange("member_Ideanext")}
                  >
                    No
                  </option>
                </select>
              </div>
              <button
                type="submit"
                className="mt-5 tracking-wide font-semibold bg-indigo-500 text-gray-100 w-full py-4 rounded-lg hover:bg-indigo-700 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none"
              >
                <i className="fas fa-user-plus fa 1x w-6  -ml-2" />
                <span className="ml-3">{textChange}</span>
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default IChallenge;
